*{
  box-sizing: border-box;
}

body{
  padding: 0;
  margin: 0;
}

/* Navbar Section CSS Start*/

.logo-image img{
  width: 200px;
  size: fixed;
}
.active{
  color: #FE9119;
  padding: 13px 0px;
  border: none;
  border-bottom: 3px solid #FE9119;
}

.nav-sections>*:hover{
  color: #FE9119;
  padding: 13px 0px;
  border: none;
  border-bottom: 3px solid #FE9119;
}

.nav-sections>*:nth-child(7):hover{
  border: none;
}

.menu:hover{
  border: none;
}
@media all and (max-width:425px) {
  .logo-image img{
    width: 166px;
    size: fixed;
  }
}

/* Navbar Section CSS End*/


/* Home Section CSS Start*/

.hiimage{
  width: 40px;
  size: fixed;
}

@media all and (max-width:479px) {
  .hiimage{
    width: 30px;
  }
}

/* #home{
  border: 1px solid red;
} */

@media all and (max-width:1055px) {
  #home{
    width: 950px;
  }
}
@media all and (max-width:985px) {
  #home{
    width: 85%;
  }
}

#home>div:nth-child(1){
  width: 47%;
  color: white;
  margin-left: 10px;
  /* border: 1px solid yellow; */
}

@media all and (max-width:991px) {
  #home>div:nth-child(1){
    width: 100%;
    margin: auto;
    text-align: center;
  }
}

#home>div:nth-child(2){
  width: 47%;
  display: flex;
  justify-content: flex-end;
  /* border: 1px solid yellowgreen; */

}

@media all and (max-width:991px) {
  #home>div:nth-child(2){
    width: 100%;
    margin: auto;
    justify-content: center;
    margin-bottom: 40px;
  }
}
.about-contact>*{
  width: 40px;
  height: 40px;
}
.about-contact>*:hover{
  width: 45px;
  height: 45px;
  transition: height 0.5s, width 0.5s;
}

#resume-button-2{
  width: 150px;
  height: 40px;
  text-align: center;
  align-items: center;
}

#resume-button-2:hover{
  width: 155px;
  height: 45px;
  transition: height 0.5s, width 0.5s;
}



#move-text{
  color: #FE9119;
}

.typed-cursor {
  color: #FE9119;
}

.home-img{
  width: 400px;
  height: 400px;
  size: fixed;
  border-radius: 50%;
  transition: transform 0.3s;
}

@media all and (max-width:991px) {
  .home-img{
    width: 300px;
    height: auto;
    size: fixed;
  }
}
@media all and (max-width:422px) {
  .home-img{
    width: 200px;
    height: auto;
    size: fixed;
  }
}

.home-img:hover {
  animation: moveInAllDirections 1s infinite;
}

@keyframes moveInAllDirections {
  0% {
    transform: translate(0, 0); /* Start with no translation */
  }
  16.6% {
    transform: translate(-5px, 5px); /* Move 5 pixels to the top-left */
  }
  49.8% {
    transform: translate(5px, -5px); /* Move 5 pixels to the bottom-right */
  }
  66.4% {
    transform: translate(-5px, -5px); /* Move 5 pixels to the top-right */
  }
  83% {
    transform: translate(5px, 5px); /* Move 5 pixels to the bottom-left */
  }
  100% {
    transform: translate(0 , 0);
  }
}


/* Home Section CSS End*/

/* About Section CSS End*/

.skill-box>*{
  text-align: center;
  font-weight: 500;
  box-shadow: 0 19px 38px rgba(0,0,0,.3), 0 15px 12px rgba(0,0,0,.22);
  padding: 30px 0px 15px 0px;
  border-radius: 10px;
  transition: .2s ease-in-out 0s;
}
.skill-box>*:hover{
  cursor: pointer;
  transform: scale(1.1);
}

.project-box,
.git-cards{
  flex-wrap: wrap;
}

.project-box>*,
.git-cards>*{
  transition: all 1s;
}
.contact_cards>*>*{
  box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
  transition: all 1s;
  font-weight: 500;
  transition: all 1s;
}
.contact_cards>*>*:hover{
  transition: all 1s;
    -webkit-transform: rotateZ(360deg);
        -ms-transform: rotateZ(360deg);
        transform: rotateZ(360deg);
}
.project-box>*:hover{
  transition: all 1s;
    -webkit-transform: rotateZ(30deg);
        -ms-transform: rotateZ(30deg);
        transform: rotateZ(5deg);
}

.git-cards>*:hover{
  transition: all .5s;
  transform: scale(1.1);
}



/* About Section CSS End*/



